import React from 'react';
import type { IIconMenuProps } from './interface';
import { BaseIconMenu } from './BaseIonMenu';
import { useToolbarItemConfig } from '../common/hooks/useToolbarItemConfig'

export type { IIconMenuProps as ITBIconMenuProps };
export const TBIconMenu = React.memo(React.forwardRef<HTMLDivElement, IIconMenuProps>(
  (props, ref) => {
    const { itemSize } = useToolbarItemConfig();

    return (
      <BaseIconMenu
        size={itemSize}
        ref={ref}
        {...props}
      />
    );
  },
));

TBIconMenu.displayName = 'iconMenu';
